div.s-block-slide-menu {
  left: 0;
  color: white;
  position: fixed;
  z-index: 20;
  height: 100%;
  background-color: #04346D;
  text-align: center;
  border: 1px solid silver;
  width: 80%;
  white-space: nowrap;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  overflow: hidden;
}

ul.s-link-menu {
  padding: 0;
}

ul.s-link-menu li {
  list-style: none;
  margin: 1.5em 0;
}

ul.s-link-menu a {
  color: white;
  text-decoration: none;
}

p.s-question-input {
  font-style: italic;
  font-weight: 300;
}

div.s-form-menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

input.s-input-menu {
  line-height: 2em;
  width: 70%;
  padding-left: 5px;
}

.s-form-menu-btn {
  margin: 2px 0 0 5px;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

span.s-menu-year {
  font-size: 12px;
  font-weight: 100;
}

@media screen and (orientation: landscape) {
  .s-menu-landscape {
    display: none;
  }
  .s-block-slide-menu {
    text-align: left !important;
    -ms-flex-pack: center;
        justify-content: center;
    width: 50% !important;
  }
}



