div.s-block-cart {
  display: -ms-flexbox;
  display: flex;
  z-index: 999;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: fixed;
  bottom: 0;
  right: 0;
  background-color: orange;
  width: 6em;
  height: 6em;
  border-radius: 6em 0 0 0;
}

div.s-block-cart a {
  display: -ms-flexbox;
  display: flex;
}

div.s-block-cart img {
  margin: 0.5em 0 0 0.5em;
}

div.s-block-cart h3 {
  position: absolute;
  font-weight: 600;
  margin: 20px 0 0 28px;
}



